@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  position: fixed;
  background-color: #eaf2ff;
  font-family: 'Poppins',
    sans-serif;
  flex-direction: column;
}

.App .DeckWrapper {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
.Navbar {
    background-color: #3b55e6;
    height: 10%;
    width: 100%;
    color: white;
    text-align: left;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.Navbar .title {
    font-size: 1.6rem;
    margin-left: 1.5%;
    font-weight: 200;
}

.Navbar .profilePic {
    height: 70%;
    border-radius: 50%;
    margin-right: 1.5%;
}
.Sidebar {
    width: 18%;
    height: 90%;
    background-color: white;
    padding-top: 1%;
    display: flex;
    justify-content: center;
}

.Sidebar .menuList {
    list-style-type: none;
    margin: 0;
    padding: 0;
    margin-left: 1%;
    width: 90%;
}

.Sidebar .menuList .menuItem {
    text-align: left;
    margin-top: 3%;
    color: #a2a7b0;
    font-size: 1.1rem;
    margin-right: 2%;
    display: flex;
    width: 100%;
}

.Sidebar .menuList .menuItem a {
    padding: 4%;
    border-radius: 15px;
    width: 100%;
    color: #a2a7b0;
    text-decoration: none;
}

.Sidebar .menuList .menuItem .active {
    background-color: #eaf2ff;
    color: #508ff4;
}

.Sidebar .menuList .menuItem i {
    margin-right: 3%;
    margin-left: 3%;
}
.Deck {
    width: 82%;
    height: 87%;
    padding: 3%;
    overflow-y: scroll;
}

.Deck .headerRow {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 2%;
}

.Deck .addContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.Deck .MuiTextField-root {
    width: 80%;
}

.Deck .addButton {
    margin-left: 2%;
    background-color: #3B55E6;
    color: white;
    padding: 1%;
    padding-left: 2%;
    padding-right: 2%;
    border-radius: 20px;
    cursor: pointer;
}
.DetailsCard {
    background-color: white;
    border-radius: 5%;
    width: 20%;
    display: flex;
    flex-direction: column;
}

.DetailsCard .number {
    font-size: 4rem;
    font-weight: 100;
}

.DetailsCard .text {
    font-size: 1rem;
    font-weight: 600;
    padding-bottom: 5%;
}
.UploadCard {
    background-color: white;
    border-radius: 5%;
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 2px solid #729fe5;
    cursor: pointer;
}

.UploadCard:hover {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.UploadCard .image {
    font-size: 4rem;
    font-weight: 100;
    margin-top: 7%;
    color: #a2a7b0;
}

.UploadCard:hover .image {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.UploadCard .text {
    font-size: 1rem;
    font-weight: 600;
    padding-bottom: 5%;
}
.DownloadCard {
    background-color: white;
    border-radius: 5%;
    width: 30%;
    display: flex;
    flex-direction: column;
    border: 2px solid #729fe5;
    cursor: pointer;
    align-items: center;
    justify-content: space-around;
}

.DownloadCard:hover {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.DownloadCard .image {
    font-size: 4rem;
    font-weight: 100;
    margin-top: 7%;
    color: #a2a7b0;
}

.DownloadCard:hover .image {
    color: #508ff4;
    transition: ease-in-out 0.2s;
}

.DownloadCard .text {
    font-size: 1rem;
    font-weight: 600;
    padding-bottom: 5%;
}

.DownloadCard .circleProgress {
    width: 70px;
    margin-top: 3%;
}
.overlay {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: black;
    opacity: 0.5;
    top: 0;
    left: 0;
    z-index: 10;
}

.uploader {
    z-index: 10;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 80%;
    width: 40%;
    background-color: white;
    border-radius: 2%;
    box-shadow: 0 3px 3px 0 rgba(196, 200, 208, 0.7);
    overflow: hidden;
}

.uploader .header {
    height: 5%;
    width: 100%;
    background-color: #508ff4;
    text-align: right;
}

/* width */
.uploader ::-webkit-scrollbar {
    width: 2px;
}

/* Track */
.uploader ::-webkit-scrollbar-track {
    border-radius: 10px;
}

/* Handle */
.uploader ::-webkit-scrollbar-thumb {
    background: #508ff4;
    border-radius: 10%;
}

/* Handle on hover */
.uploader ::-webkit-scrollbar-thumb:hover {
    background: #508ff4;
}

.fileNameContainer {
    /* align-items: center; */
    display: flex;
    justify-content: space-around;
    width: 100%;
    flex-direction: column;
    text-align: left;
    margin-left: 5%;
    margin-top: 5%;
}

.fileNameContainer .MuiFormControl-root {
    width: 90%;
}

.loader {
    border: 0.3rem solid #f3f3f3;
    border-radius: 50%;
    border-top: 0.3rem solid #3498db;
    width: 0.7rem;
    height: 0.7rem;
    -webkit-animation: spin 0.5s linear infinite;
    /* Safari */
    animation: spin 0.5s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
.ProcessedViewer {
    z-index: 10;
    border-radius: 2%;
    box-shadow: 0 3px 3px 0 rgba(196, 200, 208, 0.7);
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: transparent;
    display: flex;
    justify-content: center;
}

.ProcessedViewer .fields {
    width: 35%;
    height: 100%;
    background-color: #eaf2ff;
    padding-top: 2%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ProcessedViewer .preview {
    background-repeat: round;
    background-color: white;
    position: relative;
}

.ProcessedViewer .fields .title {
    color: #3b55e6;
    font-weight: 600;
    font-size: 2rem;
    display: flex;
}

.ProcessedViewer .fields label {
    margin-top: 5%;
    width: 80%;
    text-align: left;
}

.ProcessedViewer .fields input {
    background-color: white;
    border: 0;
    border-radius: 16px;
    width: 80%;
    padding: 2%;
    padding-left: 4%;
    caret-color: #3b55e6;
}

.ProcessedViewer .fields input:focus {
    outline: none;
}

.ProcessedViewer .crossButton {
    position: absolute;
    font-size: 1rem;
    cursor: pointer;
    right: -7%;
    top: 0.1%;
}

.ProcessedViewer .crossButton .icon-background {
    color: #3b55e6;
}
.viewTable {
